    Publisher,
    /* Skyler, */
    TheFooter,
    TheNavbar
  },
  data() {
    return {
      loggIn: false
    };
  },
  created() {
    this.fetchPage(this.$route.params.id);
    this.fetchPosts(this.$route.params.id);
    this.fetchUser(this.userAuth.uid);
  },
  methods: {
    ...mapState(["userAuth"]),
    ...mapActions(["fetchPage", "fetchPosts", "fetchUser"])
  },
  computed: {
    ...mapGetters(["getPage", "getPosts", "getUser"])
  },
  watch: {
    $route: "fetchPage"
  }
};
